.App{font-family:"Montserrat", "Avenir Next", "sans-serif";-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}#logo{position:absolute;left:0.5in;top:0.25in;z-index:9;--accent-color: white}#logo h2{font-family:brush;font-size:3em;text-shadow:0 0 20px black}a.uil{display:inline-block;transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}a.uil:hover{text-decoration:none;-webkit-transform:scale(1.2);transform:scale(1.2)}#logo:hover .menu{opacity:1;-webkit-animation:linear menu-appear 300ms;animation:linear menu-appear 300ms}

