none;-webkit-transform:translate(0, 1px);transform:translate(0, 1px)}select{-webkit-appearance:none;appearance:none;border-right-width:1rem}input[type="color"]{height:39px;padding:5px}ol,ul{list-style-type:none;padding:0.1in;margin:0}li{padding:0.1in}li *{vertical-align:middle;margin:0}a{text-decoration:none;color:#000;color:var(--accent-color);font-weight:500}a:hover{text-decoration:underline}a:active{-webkit-transform:translate(0, 2px);transform:translate(0, 2px)}input[type="checkbox"],input[type="radio"]{-webkit-appearance:initial;vertical-align:middle;background-color:rgba(186,186,186,0.498039);border:1px solid rgba(89,88,112,0.713725);border-radius:15px;margin:0.6rem 0;padding:0;height:30px;width:auto}input[type="checkbox"]{padding-right:2rem;transition:padding 200ms, background-color 300ms}input[type="checkbox"]:checked{background-color:#000;background-color:var(--accent-color);padding-left:2rem;padding-right:0;-webkit-filter:contrast(150%);filter:contrast(150%)}input[type="checkbox"]::after{content:"";display:inline-block;background-color:#f6f6f6;border-radius:14px;padding:14px}input[type="radio"]{transition:background-color 300ms}input[type="radio"]:checked{background-color:#000;background-color:var(--accent-color);-webkit-filter:contrast(150%);filter:contrast(150%)}input[type="radio"]::after{content:"";display:inline-block;border:7px solid #f6f6f6;border-radius:40px;padding:7px}input+label{margin:0.6rem}

#footer{--accent-color: white;color:white;background-color:black;background-position:center;background-repeat:no-repeat;background-size:cover;margin-top:auto;box-shadow:0 0 20px black inset;text-shadow:0 0 20px black}#footer .social-section+ul,#footer .social-section+ul li:first-child{padding-top:0}#footer ul{padding-left:0}#footer li .tooltip{position:absolute;display:none;visibility:hidden;background-color:#81e6a8;box-shadow:0 0 0.3in black;border-radius:5px;transition:opacity 300ms;padding:0.2in 0.2in;-webkit-transform:translate(0.25in, -0.25in);transform:translate(0.25in, -0.25in);width:3in;opacity:0;pointer-events:none}@media screen and (min-width: 4in) and (max-width: 7in){#footer{display:grid;grid-template-columns:1fr 1fr}#footer #copyright{grid-area:1 / 1 / 1 / 3;padding-bottom:2em;text-align:center}}@media screen and (min-width: 7in) and (max-width: 11in){#footer{display:grid;grid-template-columns:1fr 1fr 1fr;align-content:center}#footer #copyright{grid-area:1 / 1 / 1 / 4;padding-bottom:2em;text-align:center}}@media screen and (min-width: 11in){#footer{display:grid;grid-template-columns:1fr repeat(4, 2.5in) 1fr;padding:0.5in 0 1.5in}#footer #copyright{grid-area:1 / 1 / 1 / 3;padding-right:0.5in;text-align:right}#footer li .tooltip{display:inline-block}#footer li a:hover+.tooltip{visibility:visible;opacity:1}}

#display{position:absolute;left:0px;top:0px;width:100%;height:42rem;z-index:-1;overflow-x:hidden;background-color:#81e6a8;background-image:linear-gradient(-45deg, #76d9e0, #240a46);box-shadow:0 0 15px #44444444}#banner{position:absolute;background-color:black;box-shadow:0 0 15px #777;color:white;text-align:center;width:100vw;z-index:10;padding-bottom:5px}#banner h6{-webkit-background-clip:text;background-clip:text;background-image:linear-gradient(-45deg, #81e6a8, #9084e6);color:transparent;font-size:14px}#banner h6 .emoji{color:white}#projects{padding:0 6vw;margin:auto;max-width:14in}#projects img{max-width:100%;border-radius:4px;margin:0.25in auto;box-shadow:0 0 15px #44444444}#projects div{transition:-webkit-transform 300ms;transition:transform 300ms;transition:transform 300ms, -webkit-transform 300ms}#projects div.profile{margin:1.5in auto 0.5in auto;text-align:center;text-shadow:0 0 15px #444444;color:white}#projects div.profile img{border-radius:50%;max-height:2.25in}#projects h1.title{display:none}#projects div.large{margin-bottom:1in}@media screen and (min-width: 11in){#projects h1.title{display:inline;display:initial}#projects div.profile,#projects div.large{padding:0.25in}#projects div.profile{margin:1.5in auto}#projects div.large{min-height:4.5in;display:grid;grid-template-columns:1fr 5.1in;grid-template-areas:'left right';grid-gap:0.5in;align-items:center}#projects div.large img{max-height:60vh}#projects div.enhance{-webkit-transform:scale(1.05);transform:scale(1.05)}#projects div.project:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}#projects div.right{grid-template-columns:5.1in 1fr}#projects div.right img.screenshot{grid-area:right}#projects div.right div.description{grid-area:left}}.ekumen{color:#6b2875}.tag{display:inline-block;background-color:#f3f3f3;padding:6px 8px;margin:0 7px 10px 0;border-radius:4px}

